import { FunctionalComponent, h } from 'preact';
import Button from '../../components/Button';
import { DefaultUISettings } from '@usercentrics/cmp-browser-sdk';
import { ViewButtonProps } from './interface';
import { useSettingsContext } from '../../contexts/SettingsContext';
import { useTheme } from 'styled-components';

const DenyAllButton: FunctionalComponent<ViewButtonProps> = ({ disabled, onClick }) => {
  const { settings } = useSettingsContext();
  const ui = settings.ui as DefaultUISettings;
  const theme = useTheme();

  return (
    <Button
      ariaLabel={ui.ariaLabels.denyAllButton}
      backgroundColor={theme.ui.buttons.deny.bg}
      disabled={disabled}
      label={ui.buttons.denyAll.label}
      onClick={onClick}
      testid="uc-deny-all-button"
      textColor={theme.ui.buttons.deny.text}
    />
  );
};

export default DenyAllButton;
