import { FunctionalComponent, h } from 'preact';
import Button from '../../components/Button';
import { CcpaUISettings } from '@usercentrics/cmp-browser-sdk';
import { ViewButtonProps } from './interface';
import { useSettingsContext } from '../../contexts/SettingsContext';
import { useTheme } from 'styled-components';

const SaveButton: FunctionalComponent<ViewButtonProps> = ({ disabled, onClick }) => {
  const { settings } = useSettingsContext();
  const ui = settings.ui as CcpaUISettings;
  const theme = useTheme();

  return (
    <Button
      ariaLabel={ui.ariaLabels.saveButton}
      backgroundColor={theme.ui.buttons.save.bg}
      disabled={disabled}
      label={ui.buttons.save.label}
      onClick={onClick}
      testid="uc-save-button"
      textColor={theme.ui.buttons.save.text}
    />
  );
};

export default SaveButton;
